input[type=text], input[type=number] {
    width: 7%;
    padding: 12px 20px;
    margin: 8px 0;
    display: inline-block;
    border: 1px solid #ccc;
    border-radius: 4px;
    box-sizing: border-box;
    align-self: center;
  }

  .minutes {
    width: 6% !important;
  }

  .inputText {
    color: rgb(18, 143, 6);
  }

  .simulationDiv {
    border: 1px solid black;
    margin-top: 20px;
    margin-bottom: 20px;
    margin-right: 350px;
    margin-left: 350px;
  }
  
  input[type=submit], input[type=reset], .saveAndContinue, .adjustQuantity, button {
    width: 10%;
    background-color: #4CAF50;
    color: white;
    padding: 14px 20px;
    margin: 8px 0;
    border: 4px;
    border-color: black;
    border-radius: 4px;
    cursor: pointer;
    align-self: center;
  }

  th {
    padding-top: 12px;
    padding-bottom: 12px;
    text-align: center;
    background-color: #4CAF50;
    color: white;
  }

  /* tr:hover {background-color: #ddd;} */

  tr:nth-child(even){background-color: #f2f2f2;}

  th {
    padding-top: 12px;
    padding-bottom: 12px;
    text-align: center;
    background-color: #4CAF50;
    color: white;
  }

  td, th {
    border: 1px solid #ddd;
    padding: 8px;
  }

  .datatable, table {
    text-align: center;
    font-family: "Trebuchet MS", Arial, Helvetica, sans-serif;
    border-collapse: collapse;
    border: 3px solid #ddd;
    width: 100%;
  }

  .tableFooter {
    padding-top: 12px;
    padding-bottom: 12px;
    text-align: center;
    color: white;
    background-color: orangered !important;
    border: 1px solid;
  }

  .resultsDiv {
    border: 1px solid black;
    margin-top: 20px;
    margin-bottom: 20px;
    margin-right: 350px;
    margin-left: 350px;
  }

  .HODLresultsDiv {
    border: 1px solid black;
    margin-top: 20px;
    margin-bottom: 20px;
    margin-right: 350px;
    margin-left: 350px;
  }

input:disabled, input[disabled], button:disabled {
  border: 1px solid #999999;
  background-color: #cccccc;
  color: #666666;
}